import { Component, OnInit } from '@angular/core';
import * as moment from 'moment';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.scss']
})
export class HomePageComponent implements OnInit {

  name = 'Chandtesh Thakor';
  frontEndSkill = 'Html, Html5, Css, Css3, JavaScript, Angular 9+, ReactJS';
  appDevSkill = 'React Native, Flutter, TypeScript, JavaScript, Dart';
  backEndSkill = 'Node, Spring, SpringMVC,Django, Java, JavaScript, Python';
  presentCompanyJoinDate = moment('2021-03-11');
  todayDate = moment(new Date());
  exprience: {years: number , months: number , days: number} = { years : 0 , months : 0 , days : 0} ;
  constructor() {

  }

  ngOnInit(): void {
    const Moment_Exp = moment.duration(this.todayDate.diff(this.presentCompanyJoinDate));
    this.exprience  = {
      years : Math.floor(Moment_Exp.asYears()) ,
      months : Math.floor(Moment_Exp.asMonths() - (Math.floor(Moment_Exp.asYears()) * 12) ),
      days : Math.floor((Moment_Exp.asMonths() - Math.floor(Moment_Exp.asMonths())) * moment().daysInMonth())
    };
  }

  onUpPage(): void
  {
    window.scrollTo(0, 0);
  }

}
